@import './variables.scss';
@import './mixins.scss';

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-primary);
  width: 100%;
}

#root {
  height: 100%;
}

a {
  text-decoration: none;
  color: var(--color-black);
}
